<template>
	<div id="takeout-detail">
		<public-header title="订单详情"></public-header>
		<div class="content font-14">
			<div id="map-container"></div>
			<div class="tip">订单配送</div>
			<div class="bg-default">
				<div class="flex-lr padding-10-lr padding-15-tb van-hairline--bottom">
					<div class="c-primary font-500">#{{order.serial_sn}}</div>
					<div class="c-danger flex" v-if="order.status == 4">
						<template v-if="order.delivery_overtime > 0">
							距超时<count-down :endTime="order.delivery_overtime" class="c-danger"></count-down>
						</template>
						<template v-else-if="order.delivery_overtime_start > 0">
							已超时<count-down :startTime="order.delivery_overtime_start" class="c-danger"></count-down>
						</template>
					</div>
				</div>
				<div class="padding-10-lr margin-15-t flex">
					<span class="handle-type">{{order.delivery_collect_type_cn}}</span>
					<span class="delivery-time">配送 {{order.delivery_day}} {{order.delivery_time}}</span>
				</div>
				<ul class="address padding-10-lr" margin-15-t>
					<li class="flex-lr margin-15-t" v-if="order.store">
						<span class="c-info font-15">取:</span>
						<ul class="address-text">
							<li class="font-15 font-500">{{order.store.title}}</li>
							<li class="c-gray margin-5-t font-13">{{order.store.address}}</li>
						</ul>
						<i class="icon icon-locationfill" v-if="order.store.location_x && order.store.location_y" @click="util.jsLocation({lat: order.store.location_x, lng: order.store.location_y, name: order.store.title, address: order.store.address, type: MapType})"></i>
					</li>
					<li class="flex-lr margin-15-t">
						<span class="c-primary font-15">送:</span>
						<ul class="address-text">
							<li class="font-15 font-500 ">{{order.address}}</li>
							<li class="c-gray margin-5-t font-13">{{order.username}} {{order.mobile_protect}}</li>
						</ul>
						<i class="icon icon-locationfill" v-if="order.location_x && order.location_y" @click="util.jsLocation({lat: order.location_x, lng: order.location_y, address: order.address, type: MapType})"></i>
					</li>
				</ul>
				<div class="tel-group padding-10-lr padding-15-tb van-hairline--top margin-15-t flex-lr">
					<div class="tel-item flex van-hairline--right" v-if="order.store" @click="util.jsTel(order.store.telephone)">
						<div class="img-wrap">
							<img class="w-100" src="static/img/store.png" alt="">
						</div>
						<span>{{order.store.telephone}}</span>
					</div>
					<div class="tel-item flex" @click="onCallCustomer" v-if="order.data && order.data.yinsihao_status == 1">
						<div class="img-wrap">
							<img class="w-100" src="static/img/customer.png" alt="">
						</div>
						<span>{{order.mobile_protect}}</span>
					</div>
					<div class="tel-item flex" @click="util.jsTel(order.mobile)" v-else>
						<div class="img-wrap">
							<img class="w-100" src="static/img/customer.png" alt="">
						</div>
						<span>{{order.mobile}}</span>
					</div>
				</div>
			</div>
			<div class="tip">货品信息</div>
			<div class="goods-info bg-default">
				<div class="note" v-if="order.note">
					<div class="note-title">备注:</div>
					<div class="note-text">{{order.note}}</div>
				</div>
				<div class="padding-15-b padding-5-t padding-10-lr van-hairline--bottom">
					<van-row class="margin-10-t" v-for="goodsItem in order.goods" :key="goodsItem.goods_id">
						<van-col span="12">{{goodsItem.goods_title}}</van-col>
						<van-col span="3" class="text-right">x{{goodsItem.goods_num}}</van-col>
						<van-col span="9" class="text-right">{{Lang.dollarSign}}{{goodsItem.goods_price}}</van-col>
					</van-row>
				</div>
				<div class="padding-15-b padding-5-t padding-10-lr van-hairline--bottom">
					<ul class="margin-10-t c-gray flex-lr">
						<li>配送费</li>
						<li>{{Lang.dollarSign}}{{order.delivery_fee}}</li>
					</ul>
					<ul class="margin-10-t c-gray flex-lr" v-if="order.box_price > 0">
						<li>餐盒费</li>
						<li>{{Lang.dollarSign}}{{order.box_price}}</li>
					</ul>
					<ul class="margin-10-t c-gray flex-lr">
						<li>包装费</li>
						<li>{{Lang.dollarSign}}{{order.pack_fee}}</li>
					</ul>
					<ul class="margin-10-t c-gray flex-lr" v-if="order.delivery_type == 2 && order.pay_type == 'delivery'">
						<li>本单需收取顾客</li>
						<li>{{Lang.dollarSign}}{{order.final_fee}}</li>
					</ul>
					<template v-if="order.data && order.data.extra_fee" >
						<ul class="margin-10-t c-gray flex-lr" v-for="(extraFeeItem, extraFeeIndex) in order.data.extra_fee" :key="extraFeeIndex">
							<li>{{extraFeeItem.name}}</li>
							<li>{{Lang.dollarSign}}{{extraFeeItem.fee}}</li>
						</ul>
					</template>
				</div>
				<div class="padding-15-tb padding-10-lr text-right">
					<span class="c-gray font-13 margin-10-r">优惠{{Lang.dollarSign}}{{order.discount_fee}}</span>
					<span class="c-gray font-13 margin-10-r">共计{{Lang.dollarSign}}{{order.total_fee}}</span>
					<span class="font-15">
						<span>顾客实际支付</span>
						<span class="c-danger font-500">{{Lang.dollarSign}}{{order.final_fee}}</span>
					</span>
				</div>
				<div v-if="order.is_pay == 1">
					<div class="have-pay" v-if="order.pay_type != 'delivery'"></div>
					<div class="delivery-pay" v-else-if="order.pay_type == 'delivery'"></div>
				</div>
			</div>
			<div class="tip">订单信息</div>
			<div class="bg-default padding-10-lr padding-5-t padding-15-b">
				<ul class="margin-10-t flex-lr">
					<li>订单号</li>
					<li>{{order.ordersn}}</li>
				</ul>
				<ul class="margin-10-t flex-lr">
					<li>下单时间</li>
					<li>{{order.addtime_cn}}</li>
				</ul>
				<ul class="margin-10-t flex-lr">
					<li>付款方式</li>
					<li>{{order.paytype_cn}}</li>
				</ul>
				<ul class="margin-10-t flex-lr">
					<li>收入</li>
					<li>{{Lang.dollarSign}}{{order.plateform_deliveryer_fee}}</li>
				</ul>
				<template v-if="order.invoice">
					<ul class="margin-10-t flex-lr" v-if="order.invoice.title">
						<li>发票抬头</li>
						<li>{{order.invoice.title}}</li>
					</ul>
					<ul class="margin-10-t flex-lr" v-if="order.invoice.recognition">
						<li>纳税人识别号</li>
						<li>{{Lang.dollarSign}}{{order.invoice.recognition}}</li>
					</ul>
				</template>
			</div>
			<div class="times van-hairline--bottom bg-default margin-10-t padding-15 flex-lr font-14 text-center">
				<div class="time-item" v-if="order.delivery_assign_time_cn">
					<div class="time-title margin-10-b">接单</div>
					<div class="day">{{order.delivery_assign_time_cn.day}}</div>
					<div class="time margin-5-t">{{order.delivery_assign_time_cn.time}}</div>
				</div>
				<div class="time-item" v-if="order.delivery_instore_time_cn">
					<div class="time-title margin-10-b">到店</div>
					<div class="day">{{order.delivery_instore_time_cn.day}}</div>
					<div class="time margin-5-t">{{order.delivery_instore_time_cn.time}}</div>
				</div>
				<div class="time-item" v-if="order.delivery_takegoods_time_cn">
					<div class="time-title margin-10-b">取货</div>
					<div class="day">{{order.delivery_takegoods_time_cn.day}}</div>
					<div class="time margin-5-t">{{order.delivery_takegoods_time_cn.time}}</div>
				</div>
				<div class="time-item" v-if="order.delivery_success_time_cn">
					<div class="time-title margin-10-b">送达</div>
					<div class="day">{{order.delivery_success_time_cn.day}}</div>
					<div class="time margin-5-t">{{order.delivery_success_time_cn.time}}</div>
				</div>
			</div>
			<div class="btn-group padding-10-lr padding-15-t padding-5-b bg-default">
				<van-row gutter="10">
					<template v-if="order.transfer_deliveryer_id == deliveryer.id && order.transfer_delivery_status == 1">
						<van-col span="8">
							<div class="btn-item bg-danger" @click="onChangeOrderStatus('direct_transfer_refuse')">拒绝转单</div>
						</van-col>
						<van-col span="8">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('direct_transfer_agree')">接受转单</div>
						</van-col>
					</template>
					<template v-else-if="deliveryer.id == order.deliveryer_id">
						<van-col span="8" v-if="order.delivery_status == 4">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('wx_notice')">微信通知</div>
						</van-col>
						<van-col span="8" v-if="order.delivery_status == 4">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_success')">确认送达</div>
						</van-col>
						<van-col span="8" v-if="order.delivery_status == 7">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_instore')">上报到店</div>
						</van-col>
						<van-col span="8" v-if="order.delivery_status == 8 || order.delivery_status == 7">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_takegoods')">确认取货</div>
						</van-col>
						<van-col span="8" v-if="order.deliveryer_transfer_status == 1">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_transfer')">申请转单</div>
						</van-col>
						<van-col span="8" v-if="order.deliveryer_transfer_status == 1">
							<div class="btn-item bg-danger" @click="onChangeOrderStatus('direct_transfer')">定向转单</div>
						</van-col>
						<van-col span="8" v-if="order.deliveryer_cancel_status == 1">
							<div class="btn-item bg-danger" @click="onChangeOrderStatus('delivery_cancel')">取消订单</div>
						</van-col>
					</template>
				</van-row>
			</div>
			<van-dialog
				v-model="yinsihao.status"
				show-cancel-button
				title="顾客号码保护中"
				confirm-button-text="立即联系"
				@confirm="util.jsTel(yinsihao.secret_mobile)"
			>
				<div class="padding-15-tb text-center font-16">
					<span class="font-bold c-default">{{yinsihao.secret_mobile}}</span>
					<span class="padding-10-lr">转</span>
					<span class="font-bold c-danger">{{yinsihao.extension}}</span>
				</div>
				<div class="padding-15-b font-14 c-gray text-center">播打{{yinsihao.secret_mobile}}接通后，输入{{yinsihao.extension}}#即可</div>
			</van-dialog>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import countDown from "@/components/countDown"
	import {getData} from "../../controller/funcCommon"
	import {changeOrderStatus} from "./controller/order.js"
	import {Amap} from '@/config/AMap'
	import {GMap} from '@/config/GMap'
	export default {
		components: {
			publicHeader,
			countDown
		},
		data() {
			return {
				showPreLoading: true,
				order: {},
				deliveryer: {},
				config: {
					map: {
						location_x: '',
						location_y: ''
					}
				},
				yinsihao: {
					status: false,
					secret_mobile: '',
					extension: ''
				},
				MapType: this.util.getStorage('MapType') || 'gaode',
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/order/takeout/detail',
					data: {id: that.id},
					success: function(res) {
						that.order = res.order;
						that.deliveryer = res.deliveryer;
						that.config = res.config;
						that.onLoadMap();
					}
				})
			},
			onChangeOrderStatus(type) {
				var that = this;
				changeOrderStatus({
					vue: that,
					type: type,
					from: 'detail',
					order: that.order,
				})
			},
			onCallCustomer() {
				var that = this;
				getData({
					vue: that,
					url: 'yinsihao/yinsihao',
					data: {
						order_id: that.order.id,
						type: 'member',
						ordersn: that.order.ordersn
					},
					success: function(res) {
						that.yinsihao.secret_mobile = res.data.secret_mobile;
						that.yinsihao.extension = res.data.extension;
						that.yinsihao.status = true;
					}
				})
			},
			onLoadMap() {
				var that = this;
				if(that.MapType == 'gaode') {
					Amap().then(function(AMap){
						//基本地图加载
						that.map = new AMap.Map('map-container', {
							resizeEnable: true,
							center: [that.config.map.location_y, that.config.map.location_x],//地图中心点
							zoom: 13 //地图显示的缩放级别
						});
						AMap.plugin('AMap.Driving', function() {
							var driving = new AMap.Driving({
								// 驾车路线规划策略，AMap.DrivingPolicy.LEAST_TIME是最快捷模式
								policy: AMap.DrivingPolicy.LEAST_TIME,
								map: that.map,
								hideMarkers: true
							})
							let marker = {};
							let markers = [];
							var startLngLat = [that.deliveryer.location_y, that.deliveryer.location_x];
							var passLngLat = [that.order.store.location_y, that.order.store.location_x];
							var endLngLat = [that.order.location_y, that.order.location_x];
							marker = new AMap.Marker({
								position: startLngLat,
								offset: new AMap.Pixel(-13, -31),
								content: '<div class="marker-start-route"></div>'
							});
							markers.push(marker);
							marker = new AMap.Marker({
								position: endLngLat,
								offset: new AMap.Pixel(-13, -31),
								content: '<div class="marker-end-route"></div>'
							});
							markers.push(marker);
							marker = new AMap.Marker({
								position: passLngLat,
								offset: new AMap.Pixel(-13, -31),
								content: '<div class="marker-pass-route"></div>'
							});
							markers.push(marker);
							driving.search(startLngLat, endLngLat, {waypoints: [passLngLat]}, function (status, result) {
								// 未出错时，result即是对应的路线规划方案
								if (status === 'complete') {
									console.log('绘制驾车路线完成')
								} else {
									console.log('获取驾车数据失败：' + result)
								}
							})
							that.map.add(markers);
							that.map.setFitView();
						});
					});
				} else if(that.MapType == 'google') {
					var lat = parseFloat(that.config.map.location_x);
					var lng = parseFloat(that.config.map.location_y);
					GMap().then(function(google) {
						window.map = that.map = new google.maps.Map(document.getElementById('map-container'), {
							center: {lat: lat, lng: lng},
							zoom: 13,
							disableDefaultUI: true  //禁用默认UI
						});

						var startLatLng = {
							lat: parseFloat(that.deliveryer.location_x),
							lng: parseFloat(that.deliveryer.location_y)
						};
						var passLatLng = {
							lat: parseFloat(that.order.store.location_x),
							lng: parseFloat(that.order.store.location_y)
						};
						var endLatLng = {
							lat: parseFloat(that.order.location_x),
							lng: parseFloat(that.order.location_y)
						};

						//绘制路线
						var directionsService = new google.maps.DirectionsService();
						var directionsDisplay = new google.maps.DirectionsRenderer({
							suppressMarkers: true
						});
						directionsService.route({
							origin: startLatLng,
							destination: endLatLng,
							travelMode: 'DRIVING',
							waypoints: [
								{
									location: passLatLng,
									stopover: true
								}
							],
							avoidFerries: true,
							avoidHighways: true,
							avoidTolls: true,
							optimizeWaypoints: true,
						}, function(response, status) {
							if (status == 'OK') {
								directionsDisplay.setDirections(response);
								directionsDisplay.setMap(map)
							}
						});

						var marker = new google.maps.Marker({
							position: startLatLng,
							map: map,
							icon: {
								url: '../../../static/img/start.png',
								scaledSize: new google.maps.Size(26, 31),
							},
						});
						marker.setMap(map);
						var marker1 = new google.maps.Marker({
							position: passLatLng,
							map: map,
							icon: {
								url: '../../../static/img/ds.png',
								scaledSize: new google.maps.Size(26, 31),
							}
						});
						marker1.setMap(map);
						var marker2 = new google.maps.Marker({
							position: endLatLng,
							map: map,
							icon: {
								url: '../../../static/img/end.png',
								scaledSize: new google.maps.Size(26, 31),
							}
						});
						marker2.setMap(map);

					});
				}
			},
			onSetMapZoom() {
				var that = this;
				//zoom的范围值3-18
				var zoom = that.map.getZoom();
				zoom++;
				that.map.setZoomAndCenter(zoom);
			}
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.id) {
				this.id = this.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#takeout-detail #map-container{
		width: 100%;
		height: 200px;
	}
	#takeout-detail .tip{
		padding: 10px 10px;
		font-size: 14px;
		color: #7a7a7a;
	}
	#takeout-detail .handle-type{
		background-color: #FF931E;
		color: #fff;
		font-style: italic;
		padding: 3px 7px 3px 3px;
		font-size: 12px;
		border-radius: 3px;
	}
	#takeout-detail .delivery-time{
		color: #1AB5F5;
		border: 1px solid #1AB5F5;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 3px;
		margin-left: 10px;
	}
	#takeout-detail .address .icon-locationfill{
		width: 30px;
		height: 30px;
		border-radius: 50%;
		border: 1px solid #999;
		font-size: 16px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#takeout-detail .address .address-text{
		flex: 1;
		padding: 0 10px;
	}
	#takeout-detail .tel-group .tel-item{
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 15px;
	}
	#takeout-detail .tel-group .tel-item .img-wrap{
		font-size: 0px;
		width: 25px;
		margin-right: 5px;
	}
	#takeout-detail .note{
		padding: 10px;
		border-radius: 3px;
		background-color: #DFFBF9;
		color: #16C5B6;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}
	#takeout-detail .note .note-text{
		flex: 1;
		padding-left: 10px;
		line-height: 1.2;
	}
	#takeout-detail .btn-group .btn-item{
		margin-bottom: 10px;
		padding: 12px 0;
		border-radius: 5px;
		text-align: center;
		font-size: 14px;
	}
	#takeout-detail .marker-end-route{
		position: relative;
		width: 26px;
		height: 31px;
		color: #e90000;
		background: url(../../../static/img/end.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#takeout-detail .marker-start-route{
		position: relative;
		width: 26px;
		height: 31px;
		color: #e90000;
		background: url(../../../static/img/start.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#takeout-detail .marker-pass-route{
		position: relative;
		width: 26px;
		height: 31px;
		color: #e90000;
		background: url(../../../static/img/ds.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#takeout-detail .goods-info{
		position: relative;
	}
	#takeout-detail .goods-info .have-pay{
		position: absolute;
		width: 100px;
		height: 50px;
		left: 50%;
		margin-left: -50px;
		top: 100px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/have-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
	#takeout-detail .goods-info .delivery-pay{
		position: absolute;
		width: 110px;
		height: 70px;
		left: 50%;
		margin-left: -50px;
		top: 100px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/delivery-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
	#takeout-detail .van-dialog .van-dialog__confirm{
		color: #0EC3B3;
	}
</style>


